import json from '@/json'
import mixPulldownRefresh from '@/components/mix-pulldown-refresh/mix-pulldown-refresh';
import mixLoadMore from '@/components/mix-load-more/mix-load-more';
import mixItem from '@/components/mix-item/mix-item';

import {
	getBBsApi
} from '@/utils/dbhelper.js'

import {
	guid
} from '@/utils/common.js'

let windowWidth = 0,
	scrollTimer = false,
	tabBar;


export default {
	components: {
		mixPulldownRefresh,
		mixLoadMore,
		mixItem
	},
	data() {
		return {
			tabCurrentIndex: 0, //当前选项卡索引
			scrollLeft: 0, //顶部选项卡左滑距离
			enableScroll: true,
			tabBars: [],
			items: [],
			pageindex: 1
		}
	},
	async onLoad() {















		// 获取屏幕宽度
		windowWidth = uni.getSystemInfoSync().windowWidth;
		this.loadTabbars();

	},
	methods: {
		/**
		 * 数据处理方法在vue和nvue中通用，可以直接用mixin混合
		 * 这里直接写的
		 * mixin使用方法看index.nuve
		 */
		//获取分类
		loadTabbars() {
			let tabList = json.tabList;
			tabList.forEach(item => {

				item.loadMoreStatus = 0; //加载更多 0加载前，1加载中，2没有更多了
				item.refreshing = 0;
			})
			this.tabBars = tabList;
			this.loadNewsList('add');
		},
		//新闻列表
		async loadNewsList(type) {
			let tabItem = this.tabBars[this.tabCurrentIndex];
			console.log("tabItem.id", tabItem.id)
			//type add 加载更多 refresh下拉刷新
			if (type === 'add') {
				this.pageindex++;
				if (tabItem.loadMoreStatus === 2) {
					return;
				}
				tabItem.loadMoreStatus = 1;
			}






			//setTimeout模拟异步请求数据


			console.log("X", datas)
			if (type === 'refresh') {
				this.pageindex = 1

			}
			let datas = await this.getDatas(tabItem.id)

			// await uni.setStorageSync("myfavor", JSON.stringify(datas))

			if (type === 'refresh') {
				this.items = []; //刷新前清空数组
			}
			this.items = this.items.concat(datas)

			//下拉刷新 关闭刷新动画
			if (type === 'refresh') {
				this.$refs.mixPulldownRefresh && this.$refs.mixPulldownRefresh.endPulldownRefresh();



				tabItem.loadMoreStatus = 0;
			}
			//上滑加载 处理状态
			if (type === 'add') {

				tabItem.loadMoreStatus = datas.length <= 0 ? 2 : 1;
			}

		},
		//新闻详情
		navToDetails(item) {
			let data = {
				id: item.id,
				title: item.title,
				author: item.author,
				time: item.time
			}
			uni.navigateTo({
				url: `/pages/detail/detail?data=${JSON.stringify(data)}`
			})
		},

		//下拉刷新
		onPulldownReresh() {
			this.loadNewsList('refresh');
		},
		//上滑加载
		loadMore() {
			this.loadNewsList('add');
		},
		//设置scroll-view是否允许滚动，在小程序里下拉刷新时避免列表可以滑动
		setEnableScroll(enable) {
			if (this.enableScroll !== enable) {
				this.enableScroll = enable;
			}
		},

		//tab切换
		async changeTab(e) {

			if (scrollTimer) {
				//多次切换只执行最后一次
				clearTimeout(scrollTimer);
				scrollTimer = false;
			}
			let index = e;
			//e=number为点击切换，e=object为swiper滑动切换
			if (typeof e === 'object') {
				index = e.detail.current
			}
			if (typeof tabBar !== 'object') {
				tabBar = await this.getElSize("nav-bar")
			}
			//计算宽度相关
			let tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0;
			let nowWidth = 0;
			//获取可滑动总宽度
			for (let i = 0; i <= index; i++) {
				let result = await this.getElSize('tab' + i);
				width += result.width;
				if (i === index) {
					nowWidth = result.width;
				}
			}
			if (typeof e === 'number') {
				//点击切换时先切换再滚动tabbar，避免同时切换视觉错位
				this.tabCurrentIndex = index;
			}
			//延迟300ms,等待swiper动画结束再修改tabbar
			scrollTimer = setTimeout(() => {
				if (width - nowWidth / 2 > windowWidth / 2) {
					//如果当前项越过中心点，将其放在屏幕中心
					this.scrollLeft = width - nowWidth / 2 - windowWidth / 2;
				} else {
					this.scrollLeft = 0;
				}
				if (typeof e === 'object') {
					this.tabCurrentIndex = index;
				}
				this.tabCurrentIndex = index;


				//第一次切换tab，动画结束后需要加载数据
				let tabItem = this.tabBars[this.tabCurrentIndex];
				if (this.tabCurrentIndex !== 0 && tabItem.loaded !== true) {

					this.loadNewsList('refresh');
					tabItem.loaded = true;
				}
			}, 300)

		},
		//获得元素的size
		getElSize(id) {
			return new Promise((res, rej) => {
				let el = uni.createSelectorQuery().select('#' + id);
				el.fields({
					size: true,
					scrollOffset: true,
					rect: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		async getDatas(_id) {

			console.log("getdata", _id, this.pageindex)

			const sign = guid()
			let res = await getBBsApi({
				type: "list",
				fid: _id,
				page: this.pageindex,
				limit: 50,
				order: 0,
				system: "wechat",
				sign: sign,
			}, "bbsapi").catch(error => {

			})


			if (res && res.data && res.data.length > 0) {
				return res.data.map(p => {
					return {
						id: p.bbsId,
						title: p.title,
						type: p.img_path ? 1 : 0,
						images: p.img_path ? [p.preview] : [],
						rtime: p.postTime,
						time: p.replyTime,
						author: p.nickName,
						view: p.view,
						reply: p.reply
					}
				})



			} else {
				return []
			}

		},
		gotoFavor() {
			uni.navigateTo({
				url: `/pages/favor/favor`
			})
		}
	}
}
